import { Component, h, Prop } from '@stencil/core';
import { articles, ArticlesData } from "../../../global/data/articles";

@Component({
    tag: 'ks-page-articles',
    styleUrl: 'articles.css'
})
export class PageArticles{
    @Prop() skipbase: boolean;
    @Prop() commonData: string;
    @Prop() commonDynamicData: string;

    @Prop() articlesData: string;

    componentWillLoad() {
        const articlesDataElement = document.getElementById(this.articlesData);
		const articlesData = JSON.parse(articlesDataElement.innerHTML) as ArticlesData;

        Object.keys(articlesData).map(key => {
			articles.set(key as keyof ArticlesData, articlesData[key]);
		});
    }

	render() {
        const navigation = articles.get('navigation');
        const cards = articles.get('articles');

		return <ks-page-base skipbase={this.skipbase} commonData={this.commonData} commonDynamicData={this.commonDynamicData}>
            
            <h1>{articles.get('heading')}</h1>

            {navigation && navigation.count > 1 ?
                <ks-listing-navigation>
                    <ks-pagination count={navigation.count} current={navigation.current} base={navigation.paginationBase} pattern={navigation.pattern} />
                </ks-listing-navigation>
            : null}

            {cards?.length > 0 ?
                <ks-article-container>
                    {cards.map(card => 
                        <ks-article-card 
                            link={card.link}
                            heading={card.heading}
                            image={card.image}
                            webp={card.webp}
                            date={card.date}
                            views={card.views}>
                        <p>{card.description}</p>
                    </ks-article-card>
                    )}
                </ks-article-container>
            : null}

            {navigation && cards?.length > 0 ?
                <ks-listing-navigation>
                    <ks-pagination count={navigation.count} current={navigation.current} base={navigation.paginationBase} pattern={navigation.pattern} />
                </ks-listing-navigation>
            : null}

        </ks-page-base>;
	}
}